// pages/CustomerDetail.js
import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
// import axios from '../../api/axios'; // Update this import
import {
  Box, Paper, Typography, Grid, Divider, List, ListItem, ListItemText,
  Button, Tabs, Tab, CircularProgress, Card, CardContent, Chip, IconButton,
  Dialog, DialogTitle, DialogContent, DialogActions, TextField
} from '@mui/material';
import { 
  Edit as EditIcon, 
  Delete as DeleteIcon,
  Email as EmailIcon,
  Phone as PhoneIcon,
  LocationOn as LocationIcon
} from '@mui/icons-material';
import axios from 'axios';

const CustomerDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [customer, setCustomer] = useState(null);
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [tabValue, setTabValue] = useState(0);
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    address: ''
  });

  useEffect(() => {
    const fetchCustomerData = async () => {
      try {
        const token = localStorage.getItem('token');
        const config = {
          headers: {
            'x-auth-token': token
          }
        };
        
        const [customerRes, ordersRes] = await Promise.all([
          axios.get(`/customers/${id}`, config),
          axios.get(`/customers/${id}/orders`, config)
        ]);
        
        setCustomer(customerRes.data);
        setOrders(ordersRes.data);
        
        // Pre-populate form data for editing
        setFormData({
          name: customerRes.data.name,
          email: customerRes.data.email,
          phone: customerRes.data.phone || '',
          address: customerRes.data.address || ''
        });
      } catch (error) {
        console.error('Error fetching customer data:', error);
      } finally {
        setLoading(false);
      }
    };
    
    fetchCustomerData();
  }, [id]);

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const handleEditDialogOpen = () => {
    setEditDialogOpen(true);
  };

  const handleEditDialogClose = () => {
    setEditDialogOpen(false);
  };

  const handleFormChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleUpdateCustomer = async () => {
    try {
      const token = localStorage.getItem('token');
      const config = {
        headers: {
          'Content-Type': 'application/json',
          'x-auth-token': token
        }
      };
      
      const res = await axios.put(`/customers/${id}`, formData, config);
      setCustomer(res.data);
      setEditDialogOpen(false);
    } catch (error) {
      console.error('Error updating customer:', error);
    }
  };

  const handleDeleteCustomer = async () => {
    if (window.confirm('Are you sure you want to delete this customer? This action cannot be undone.')) {
      try {
        const token = localStorage.getItem('token');
        const config = {
          headers: {
            'x-auth-token': token
          }
        };
        
        await axios.delete(`/customers/${id}`, config);
        navigate('/customers');
      } catch (error) {
        console.error('Error deleting customer:', error);
      }
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  if (!customer) {
    return (
      <Box sx={{ mt: 4 }}>
        <Typography variant="h5" color="error">
          Customer not found
        </Typography>
        <Button 
          variant="contained" 
          onClick={() => navigate('/customers')}
          sx={{ mt: 2 }}
        >
          Back to Customers
        </Button>
      </Box>
    );
  }

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h4" gutterBottom>
          {customer.name}
        </Typography>
        <Box>
          <IconButton 
            color="primary" 
            onClick={handleEditDialogOpen}
            sx={{ mr: 1 }}
          >
            <EditIcon />
          </IconButton>
          <IconButton 
            color="error" 
            onClick={handleDeleteCustomer}
          >
            <DeleteIcon />
          </IconButton>
        </Box>
      </Box>
      
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Paper elevation={2} sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
              Customer Information
            </Typography>
            <Divider sx={{ mb: 2 }} />
            
            <List dense>
              <ListItem>
                <EmailIcon sx={{ mr: 2, color: 'text.secondary' }} />
                <ListItemText 
                  primary="Email" 
                  secondary={customer.email} 
                />
              </ListItem>
              <ListItem>
                <PhoneIcon sx={{ mr: 2, color: 'text.secondary' }} />
                <ListItemText 
                  primary="Phone" 
                  secondary={customer.phone || 'Not provided'} 
                />
              </ListItem>
              <ListItem>
                <LocationIcon sx={{ mr: 2, color: 'text.secondary' }} />
                <ListItemText 
                  primary="Address" 
                  secondary={customer.address || 'Not provided'} 
                />
              </ListItem>
            </List>
            
            <Divider sx={{ my: 2 }} />
            
            <Typography variant="subtitle2" color="text.secondary" gutterBottom>
              Customer since
            </Typography>
            <Typography variant="body1">
              {new Date(customer.createdAt).toLocaleDateString()}
            </Typography>
            
            <Box sx={{ mt: 2 }}>
              <Typography variant="subtitle2" color="text.secondary" gutterBottom>
                Last active
              </Typography>
              <Typography variant="body1">
                {new Date(customer.lastActive).toLocaleDateString()}
              </Typography>
            </Box>
          </Paper>
          
          <Paper elevation={2} sx={{ p: 3, mt: 3 }}>
            <Typography variant="h6" gutterBottom>
              Customer Metrics
            </Typography>
            <Divider sx={{ mb: 2 }} />
            
            <Grid container spacing={1}>
              <Grid item xs={6}>
                <Typography variant="subtitle2" color="text.secondary">
                  Total Spend
                </Typography>
                <Typography variant="h5">
                  ${customer.totalSpend.toLocaleString()}
                </Typography>
              </Grid>
              <Grid item xs={6}>
                <Typography variant="subtitle2" color="text.secondary">
                  Order Count
                </Typography>
                <Typography variant="h5">
                  {orders.length}
                </Typography>
              </Grid>
              <Grid item xs={6} sx={{ mt: 2 }}>
                <Typography variant="subtitle2" color="text.secondary">
                  Avg. Order Value
                </Typography>
                <Typography variant="h5">
                  ${orders.length > 0 
                    ? (customer.totalSpend / orders.length).toFixed(2) 
                    : '0.00'}
                </Typography>
              </Grid>
              <Grid item xs={6} sx={{ mt: 2 }}>
                <Typography variant="subtitle2" color="text.secondary">
                  Total Visits
                </Typography>
                <Typography variant="h5">
                  {customer.visits || 0}
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        
        <Grid item xs={12} md={8}>
          <Paper elevation={2} sx={{ p: 2 }}>
            <Tabs value={tabValue} onChange={handleTabChange}>
              <Tab label="Orders" />
              <Tab label="Activity" />
              <Tab label="Segments" />
            </Tabs>
            
            <Box sx={{ p: 2, mt: 1 }}>
              {tabValue === 0 && (
                <Box>
                  {orders.length === 0 ? (
                    <Typography variant="body1" color="text.secondary" sx={{ py: 2 }}>
                      No order history found
                    </Typography>
                  ) : (
                    orders.map((order) => (
                      <Card key={order._id} sx={{ mb: 2 }}>
                        <CardContent>
                          <Grid container spacing={2}>
                            <Grid item xs={8}>
                              <Typography variant="subtitle1">
                                Order #{order.orderNumber || order._id.slice(-6)}
                              </Typography>
                              <Typography variant="body2" color="text.secondary">
                                {new Date(order.orderDate).toLocaleDateString()} at {new Date(order.orderDate).toLocaleTimeString()}
                              </Typography>
                            </Grid>
                            <Grid item xs={4} sx={{ textAlign: 'right' }}>
                              <Typography variant="h6">
                                ${order.amount.toLocaleString()}
                              </Typography>
                              <Chip 
                                label={order.status}
                                color={
                                  order.status === 'completed' ? 'success' :
                                  order.status === 'cancelled' ? 'error' : 'warning'
                                }
                                size="small"
                                sx={{ mt: 1 }}
                              />
                            </Grid>
                          </Grid>
                          
                          {order.items && (
                            <Box sx={{ mt: 2 }}>
                              <Typography variant="body2" color="text.secondary">
                                {order.items.length} {order.items.length === 1 ? 'item' : 'items'}
                              </Typography>
                            </Box>
                          )}
                        </CardContent>
                      </Card>
                    ))
                  )}
                </Box>
              )}
              
              {tabValue === 1 && (
                <Box>
                  <Typography variant="body1" color="text.secondary" sx={{ py: 2 }}>
                    Activity tracking feature coming soon
                  </Typography>
                </Box>
              )}
              
              {tabValue === 2 && (
                <Box>
                  {customer.segments && customer.segments.length > 0 ? (
                    <List>
                      {customer.segments.map((segment) => (
                        <ListItem key={segment._id}>
                          <ListItemText 
                            primary={segment.name}
                            secondary={`Added on ${new Date(segment.addedAt).toLocaleDateString()}`}
                          />
                        </ListItem>
                      ))}
                    </List>
                  ) : (
                    <Typography variant="body1" color="text.secondary" sx={{ py: 2 }}>
                      Customer is not in any segments
                    </Typography>
                  )}
                </Box>
              )}
            </Box>
          </Paper>
        </Grid>
      </Grid>
      
      {/* Edit Customer Dialog */}
      <Dialog open={editDialogOpen} onClose={handleEditDialogClose} maxWidth="sm" fullWidth>
        <DialogTitle>Edit Customer</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            name="name"
            label="Name"
            type="text"
            fullWidth
            variant="outlined"
            value={formData.name}
            onChange={handleFormChange}
            sx={{ mb: 2, mt: 1 }}
          />
          <TextField
            margin="dense"
            id="email"
            name="email"
            label="Email Address"
            type="email"
            fullWidth
            variant="outlined"
            value={formData.email}
            onChange={handleFormChange}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            id="phone"
            name="phone"
            label="Phone Number"
            type="tel"
            fullWidth
            variant="outlined"
            value={formData.phone}
            onChange={handleFormChange}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            id="address"
            name="address"
            label="Address"
            type="text"
            fullWidth
            variant="outlined"
            multiline
            rows={2}
            value={formData.address}
            onChange={handleFormChange}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleEditDialogClose}>Cancel</Button>
          <Button onClick={handleUpdateCustomer} variant="contained">Save Changes</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default CustomerDetail;