// pages/Dashboard.js
import React, { useEffect, useState } from 'react';
import { Box, Grid, Paper, Typography, Divider, CircularProgress } from '@mui/material';
import axios from '../api/axios'; // Updated import to use configured axios instance

const Dashboard = () => {
  const [stats, setStats] = useState({
    totalCustomers: 0,
    totalSpend: 0,
    activeCustomers: 0,
    recentOrders: []
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchDashboardData = async () => {
      try {
        const token = localStorage.getItem('token');
        const config = {
          headers: {
            'x-auth-token': token
          }
        };
        
        const [customersRes, ordersRes] = await Promise.all([
          axios.get('/customers', config),
          axios.get('/orders', config)
        ]);
        
        const customers = customersRes.data;
        const orders = ordersRes.data;
        
        // Calculate stats
        const activeCustomers = customers.filter(c => {
          const lastActive = new Date(c.lastActive);
          const thirtyDaysAgo = new Date();
          thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);
          return lastActive > thirtyDaysAgo;
        });
        
        setStats({
          totalCustomers: customers.length,
          totalSpend: customers.reduce((sum, customer) => sum + customer.totalSpend, 0),
          activeCustomers: activeCustomers.length,
          recentOrders: orders.slice(0, 5)
        });
      } catch (error) {
        console.error('Error fetching dashboard data:', error);
      } finally {
        setLoading(false);
      }
    };
    
    fetchDashboardData();
  }, []);

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Dashboard
      </Typography>
      
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Paper elevation={2} sx={{ p: 3, textAlign: 'center' }}>
            <Typography variant="h6" color="text.secondary">
              Total Customers
            </Typography>
            <Typography variant="h3" sx={{ mt: 2 }}>
              {stats.totalCustomers}
            </Typography>
          </Paper>
        </Grid>
        
        <Grid item xs={12} md={4}>
          <Paper elevation={2} sx={{ p: 3, textAlign: 'center' }}>
            <Typography variant="h6" color="text.secondary">
              Total Revenue
            </Typography>
            <Typography variant="h3" sx={{ mt: 2 }}>
              ${stats.totalSpend.toLocaleString()}
            </Typography>
          </Paper>
        </Grid>
        
        <Grid item xs={12} md={4}>
          <Paper elevation={2} sx={{ p: 3, textAlign: 'center' }}>
            <Typography variant="h6" color="text.secondary">
              Active Customers (30d)
            </Typography>
            <Typography variant="h3" sx={{ mt: 2 }}>
              {stats.activeCustomers}
            </Typography>
          </Paper>
        </Grid>
        
        <Grid item xs={12}>
          <Paper elevation={2} sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
              Recent Orders
            </Typography>
            <Divider sx={{ mb: 2 }} />
            
            {stats.recentOrders.length > 0 ? (
              stats.recentOrders.map((order) => (
                <Box key={order._id} sx={{ py: 1.5, borderBottom: '1px solid #eee' }}>
                  <Grid container>
                    <Grid item xs={4}>
                      <Typography variant="body1">
                        {order.customer?.name || 'Unknown'}
                      </Typography>
                      <Typography variant="body2" color="text.secondary">
                        {new Date(order.orderDate).toLocaleDateString()}
                      </Typography>
                    </Grid>
                    <Grid item xs={4}>
                      <Typography variant="body1">
                        ${order.amount.toLocaleString()}
                      </Typography>
                    </Grid>
                    <Grid item xs={4}>
                      <Typography variant="body2" 
                        sx={{ 
                          textTransform: 'uppercase',
                          color: order.status === 'completed' ? 'success.main' : 
                                 order.status === 'cancelled' ? 'error.main' : 'warning.main'
                        }}
                      >
                        {order.status}
                      </Typography>
                    </Grid>
                  </Grid>
                </Box>
              ))
            ) : (
              <Typography variant="body1" color="text.secondary" sx={{ py: 2 }}>
                No recent orders
              </Typography>
            )}
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Dashboard;
