// pages/SegmentList.js
import React, { useState, useEffect } from 'react';
import { 
  Box, Paper, Typography, Table, TableBody, TableCell, TableContainer,
  TableHead, TableRow, Button, Chip 
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import AddIcon from '@mui/icons-material/Add';
import axios from '../api/axios'; // Updated import to use configured axios instance

const SegmentList = () => {
  const [segments, setSegments] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchSegments = async () => {
      try {
        const token = localStorage.getItem('token');
        const config = {
          headers: {
            'x-auth-token': token
          }
        };
        
        const res = await axios.get('/segments', config);
        setSegments(res.data);
      } catch (error) {
        console.error('Error fetching segments:', error);
      } finally {
        setLoading(false);
      }
    };
    
    fetchSegments();
  }, []);

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h4" gutterBottom>
          Customer Segments
        </Typography>
        <Button 
          variant="contained" 
          startIcon={<AddIcon />}
          onClick={() => navigate('/segments/new')}
        >
          Create Segment
        </Button>
      </Box>
      
      <Paper elevation={2} sx={{ width: '100%' }}>
        <TableContainer>
          <Table sx={{ minWidth: 650 }}>
            <TableHead>
              <TableRow>
                <TableCell>Segment Name</TableCell>
                <TableCell align="right">Audience Size</TableCell>
                <TableCell>Created</TableCell>
                <TableCell>Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {loading ? (
                <TableRow>
                  <TableCell colSpan={4} align="center">Loading...</TableCell>
                </TableRow>
              ) : segments.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={4} align="center">No segments found</TableCell>
                </TableRow>
              ) : (
                segments.map((segment) => (
                  <TableRow 
                    key={segment._id}
                    hover
                  >
                    <TableCell>{segment.name}</TableCell>
                    <TableCell align="right">
                      <Chip 
                        label={`${segment.audienceSize.toLocaleString()} customers`}
                        color="primary"
                        size="small"
                      />
                    </TableCell>
                    <TableCell>
                      {new Date(segment.createdAt).toLocaleDateString()}
                    </TableCell>
                    <TableCell>
                      <Button 
                        variant="outlined"
                        size="small"
                        onClick={() => navigate(`/campaigns/new?segmentId=${segment._id}`)}
                      >
                        Create Campaign
                      </Button>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </Box>
  );
};

export default SegmentList;