// pages/CampaignList.js
import React, { useState, useEffect } from 'react';
// import axios from '../../api/axios'; // Update this import
import { 
  Box, Paper, Typography, Table, TableBody, TableCell, TableContainer, 
  TableHead, TableRow, Button, Chip 
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import AddIcon from '@mui/icons-material/Add';
import axios from '../api/axios';

const getStatusColor = (status) => {
  switch (status) {
    case 'draft':
      return 'default';
    case 'scheduled':
      return 'info';
    case 'sending':
      return 'warning';
    case 'completed':
      return 'success';
    default:
      return 'default';
  }
};

const CampaignList = () => {
  const [campaigns, setCampaigns] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchCampaigns = async () => {
      try {
        const token = localStorage.getItem('token');
        const config = {
          headers: {
            'x-auth-token': token
          }
        };
        
        const res = await axios.get('/campaigns', config);
        setCampaigns(res.data);
      } catch (error) {
        console.error('Error fetching campaigns:', error);
      } finally {
        setLoading(false);
      }
    };
    
    fetchCampaigns();
  }, []);

  const handleSendCampaign = async (campaignId) => {
    try {
      const token = localStorage.getItem('token');
      const config = {
        headers: {
          'Content-Type': 'application/json',
          'x-auth-token': token
        }
      };
      
      await axios.post(`/api/campaigns/${campaignId}/send`, {}, config);
      
      // Update the campaign status in the UI
      setCampaigns(campaigns.map(campaign => 
        campaign._id === campaignId 
          ? { ...campaign, status: 'sending' }
          : campaign
      ));
    } catch (error) {
      console.error('Error sending campaign:', error);
    }
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h4" gutterBottom>
          Campaigns
        </Typography>
        <Button 
          variant="contained" 
          startIcon={<AddIcon />}
          onClick={() => navigate('/campaigns/new')}
        >
          Create Campaign
        </Button>
      </Box>
      
      <Paper elevation={2} sx={{ width: '100%' }}>
        <TableContainer>
          <Table sx={{ minWidth: 650 }}>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>Segment</TableCell>
                <TableCell>Status</TableCell>
                <TableCell align="right">Recipients</TableCell>
                <TableCell align="right">Sent</TableCell>
                <TableCell>Created</TableCell>
                <TableCell>Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {loading ? (
                <TableRow>
                  <TableCell colSpan={7} align="center">Loading...</TableCell>
                </TableRow>
              ) : campaigns.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={7} align="center">No campaigns found</TableCell>
                </TableRow>
              ) : (
                campaigns.map((campaign) => (
                  <TableRow 
                    key={campaign._id}
                    hover
                  >
                    <TableCell>{campaign.name}</TableCell>
                    <TableCell>{campaign.segment?.name || 'Unknown segment'}</TableCell>
                    <TableCell>
                      <Chip 
                        label={campaign.status}
                        color={getStatusColor(campaign.status)}
                        size="small"
                      />
                    </TableCell>
                    <TableCell align="right">
                      {campaign.recipientCount || campaign.segment?.audienceSize || 0}
                    </TableCell>
                    <TableCell align="right">
                      {campaign.sentCount || 0}
                    </TableCell>
                    <TableCell>
                      {new Date(campaign.createdAt).toLocaleDateString()}
                    </TableCell>
                    <TableCell>
                      {campaign.status === 'draft' && (
                        <Button 
                          variant="outlined"
                          size="small"
                          color="primary"
                          onClick={() => handleSendCampaign(campaign._id)}
                        >
                          Send
                        </Button>
                      )}
                      {campaign.status === 'sending' && (
                        <Typography variant="caption" color="text.secondary">
                          In progress...
                        </Typography>
                      )}
                      {campaign.status === 'completed' && (
                        <Button 
                          variant="outlined"
                          size="small"
                          onClick={() => navigate(`/campaigns/${campaign._id}/report`)}
                        >
                          View Report
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </Box>
  );
};

export default CampaignList;